
body {
  
  padding: 5px;
  font-family: Helvetica;
}

#app {
  background: #fff;
  border-radius: 4px;
  padding: 35px;
}

li {
  margin: 8px 0;
}

h2 {
  font-weight: bold;
  margin-bottom: 15px;
}


ul {
  list-style-type: circle;
}

ul {
  list-style-type: square;
}

ol {
  list-style-type: decimal;
}
main {
  display: flex;
  justify-content: center;
}

table {
  max-width: 100%;
}

tr:nth-child(odd) {
  background-color: #eee;
}

th {
  background-color: #555;
  color: #fff;
}

th,
td {
  text-align: left;
  padding: 0.5em 1em;
}

p {
  text-align: left;
  padding: 0.1em 0.2em;
}
